.bodyContainer {
  margin: 0;
  padding: 0;
  background: linear-gradient(to bottom right, #ffffff, #fafcff);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cartContainer {
  width: 70%;
  height: auto;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 0px 25px 40px #1687d933;
  padding: 5px;
}

.cartHeader {
  margin: auto;
  width: 90%;
  height: 15%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.heading {
  font-size: 14px;
  font-family: sans-serif;
  font-weight: bold;
  color: #2f3841;
}
.action {
  font-size: 14px;
  font-family: sans-serif;
  font-weight: bold;
  color: #e44c4c;
  cursor: pointer;
  border-bottom: 1px solid #e44c4c;
}

.cartItems {
  margin: auto;
  width: 90%;
  height: 30%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.imageBox {
  width: 15%;
  text-align: center;
}

.about {
  height: 100%;
}

.title {
  padding-top: 5px;
  line-height: 10px;
  font-size: 18px;
  font-family: sans-serif;
  font-weight: bold;
  color: #202020;
}

.subtitle {
  line-height: 10px;
  font-size: 16px;
  font-family: sans-serif;
  font-weight: bold;
  color: #909090;
}

.counter {
  width: 15%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #d9d9d9;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-family: sans-serif;
  font-weight: bold;
  color: #202020;
  cursor: pointer;
}
.count {
  font-size: 16px;
  font-family: sans-serif;
  font-weight: bold;
  color: #202020;
}

.prices {
  height: 100%;
  text-align: right;
}
.amount {
  padding-top: 20px;
  font-size: 20px;
  font-family: sans-serif;
  font-weight: bold;
  color: #202020;
}
.save {
  padding-top: 5px;
  font-size: 14px;
  font-family: sans-serif;
  font-weight: bold;
  color: #1687d9;
  cursor: pointer;
}
.remove {
  padding-top: 5px;
  font-size: 14px;
  font-family: sans-serif;
  font-weight: bold;
  color: #e44c4c;
  cursor: pointer;
}

hr {
  width: 66%;
  float: right;
  margin-right: 5%;
}
.checkout {
  float: right;
  margin-right: 5%;
  width: 28%;
}
.total {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.Subtotal {
  font-size: 18px;
  font-family: sans-serif;
  font-weight: bold;
  color: #202020;
}
.items {
  font-size: 16px;
  font-family: sans-serif;
  font-weight: bold;
  color: #909090;
  line-height: 10px;
}
.total-amount {
  font-size: 26px;
  font-family: sans-serif;
  font-weight: bold;
  color: #202020;
}
.CartButton {
  margin-top: 5px;
  width: 100%;
  height: 40px;
  border: none;
  background: linear-gradient(to bottom right, #b8d7ff, #8eb7eb);
  border-radius: 20px;
  cursor: pointer;
  font-size: 16px;
  font-family: sans-serif;
  font-weight: bold;
  color: #202020;
}
